	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import shiyanshiyuyue from '@/views/shiyanshiyuyue/list'
	import discussshiyanshixinxi from '@/views/discussshiyanshixinxi/list'
	import yaopinruku from '@/views/yaopinruku/list'
	import storeup from '@/views/storeup/list'
	import discussshiyankecheng from '@/views/discussshiyankecheng/list'
	import yaopinchuku from '@/views/yaopinchuku/list'
	import discussyiqishebei from '@/views/discussyiqishebei/list'
	import shiyanshuju from '@/views/shiyanshuju/list'
	import huaxuepinshenbao from '@/views/huaxuepinshenbao/list'
	import shijiyaopin from '@/views/shijiyaopin/list'
	import yonghu from '@/views/yonghu/list'
	import shiyanyuyue from '@/views/shiyanyuyue/list'
	import shiyanshixinxi from '@/views/shiyanshixinxi/list'
	import shiyanshileixing from '@/views/shiyanshileixing/list'
	import shebeijieyong from '@/views/shebeijieyong/list'
	import config from '@/views/config/list'
	import shiyankecheng from '@/views/shiyankecheng/list'
	import yiqishebei from '@/views/yiqishebei/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/news',
			name: '实验室日常',
			component: news
		}
		,{
			path: '/shiyanshiyuyue',
			name: '实验室预约',
			component: shiyanshiyuyue
		}
		,{
			path: '/discussshiyanshixinxi',
			name: '实验室信息评论',
			component: discussshiyanshixinxi
		}
		,{
			path: '/yaopinruku',
			name: '药品入库',
			component: yaopinruku
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/discussshiyankecheng',
			name: '实验课程评论',
			component: discussshiyankecheng
		}
		,{
			path: '/yaopinchuku',
			name: '药品出库',
			component: yaopinchuku
		}
		,{
			path: '/discussyiqishebei',
			name: '仪器设备评论',
			component: discussyiqishebei
		}
		,{
			path: '/shiyanshuju',
			name: '实验数据',
			component: shiyanshuju
		}
		,{
			path: '/huaxuepinshenbao',
			name: '化学品申报',
			component: huaxuepinshenbao
		}
		,{
			path: '/shijiyaopin',
			name: '试剂药品',
			component: shijiyaopin
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/shiyanyuyue',
			name: '实验预约',
			component: shiyanyuyue
		}
		,{
			path: '/shiyanshixinxi',
			name: '实验室信息',
			component: shiyanshixinxi
		}
		,{
			path: '/shiyanshileixing',
			name: '实验室类型',
			component: shiyanshileixing
		}
		,{
			path: '/shebeijieyong',
			name: '设备借用',
			component: shebeijieyong
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/shiyankecheng',
			name: '实验课程',
			component: shiyankecheng
		}
		,{
			path: '/yiqishebei',
			name: '仪器设备',
			component: yiqishebei
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
